import { useState, useEffect } from "react";
import ReactHtmlParser from "react-html-parser";
import React from 'react';
import ReactLoading from "react-loading";
import "bootstrap/dist/css/bootstrap.css";

function Detail(props) {

  const [loading, setLoading] = useState(true);

  const [item, setItem] = useState({});


  const Movie = (
    
       <div className="detail-main-container">
          <div className="detail-banner">
            <img
              className="detail-banner"
              src={item.image && item.image.original}
              alt={item.name}
            />
          </div>
          <div className="detail-name"> {item.name} </div>
          <hr />
          <div className="summary"> {ReactHtmlParser(item.summary)} </div>
        </div>
    
  );

  useEffect(() => {
    getApıData();
  }, []);


  const getApıData = ()=>{
    fetch(`https://api.tvmaze.com/shows/${props.match.params.id}`, {method: 'GET'})
      .then(res => res.json())
      .then(
        item => setItem({ ...item }),
        setLoading(false)
      );
  }

  

  return (
    <div>
      {loading ? <ReactLoading style={{'marginLeft': '450px'}} type={"bars"} color={"black"} /> : Movie}
    </div>
  );
}




export default Detail;
